.post-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Inputs */
.post-form input,
.post-form textarea,
.post-form select {
  background-color: #0d0d0d;
  border: 1px solid #00ff00;
  color: #ffffff;
  padding: 10px 14px;
  border-radius: 6px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
}

.post-form textarea {
  height: 100px;
  resize: vertical;
}

/* Labels */
.post-form .form-label {
  font-weight: bold;
  color: #00ff00;
}

/* Submit button */
.post-form .submit-btn {
  background-color: #00ff00;
  color: #000;
  border: none;
  padding: 12px;
  border-radius: 8px;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.post-form .submit-btn:hover {
  background-color: #33ff33;
}

/* Toggle icon buttons */
.toggle-buttons-row {
  display: flex;
  gap: 10px;
  margin: 10px 0;
  justify-content: center;
}

.toggle-buttons-row img {
  width: 48px;
  height: 48px;
  background-color: #1a1a1a;
  border-radius: 50%;
  padding: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

.toggle-buttons-row img:hover {
  background-color: #00ff00;
  transform: scale(1.05);
}

.toggle-buttons-row img.active {
  background-color: #00ff00;
  transform: scale(1.1);
}

/* Hidden fields */
.hidden-input {
  display: none;
}

/* Upload container */
.upload-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.preview-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.preview-media {
  border: 2px solid #00ff00;
  border-radius: 8px;
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Uploading text */
.uploading-text {
  color: #999;
  font-style: italic;
}

/* Success message */
.post-success-message {
  background-color: #0d0d0d;
  color: #00ff00;
  padding: 20px;
  border-radius: 10px;
  font-weight: bold;
  text-align: center;
  font-size: 1.1rem;
}

/* Autocomplete suggestions */
.autocomplete-suggestions {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #0d0d0d;
  border: 1px solid #00ff00;
  border-radius: 6px;
  max-height: 200px;
  overflow-y: auto;
  position: relative;
  z-index: 10;
}

.autocomplete-suggestions li {
  padding: 10px 14px;
  cursor: pointer;
  color: #ffffff;
  border-bottom: 1px solid #1a1a1a;
}

.autocomplete-suggestions li:hover {
  background-color: #00ff00;
  color: #000;
}

/* Load Details section */
.load-details-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Price input styling with $ sign */
.truck-price-wrapper {
  position: relative;
  width: 100%;
}

.truck-price-wrapper input {
  width: 100%;
  background-color: #0d0d0d;
  border: 1px solid #00ff00;
  color: #ffffff;
  border-radius: 6px;
  font-size: 1rem;
  box-sizing: border-box;
}

/* Routes section */
.routes-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Chips for routes */
.chips-container {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 5px;
}

.chip {
  display: flex;
  align-items: center;
  background-color: #1a1a1a;
  color: #00ff00;
  padding: 4px 10px;
  border-radius: 16px;
  font-size: 0.9rem;
}

.chip-close {
  margin-left: 6px;
  cursor: pointer;
  font-weight: bold;
}